  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-LIGHT.TTF') format('truetype');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-REGULAR.TTF') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-MEDIUM.TTF') format('truetype');
  font-weight: 500;
  font-style: normal;
