@font-face {
  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-SEMIBOLD.TTF') format('truetype');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-BOLD.TTF') format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Goldplay';
  src: url('../assets/fonts/GOLDPLAY-BLACK.TTF') format('truetype');
  font-weight: 900;
  font-style: normal;
}

html {
  font-size: 16px;
}

body {
  font-family: 'Goldplay', sans-serif;
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  line-height: 1.6;
}

h1 {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 700;
}

h2 {
  font-size: clamp(1.75rem, 4vw, 2.5rem);
  font-weight: 600;
}

h3 {
  font-size: clamp(1.5rem, 3.5vw, 2rem);
  font-weight: 500;
}

p {
  font-size: clamp(0.875rem, 2vw, 1rem);
  font-weight: 400;
  line-height: 1.8;
}

.login-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f9f9f9;
  font-family: Arial, sans-serif;
}

@media (min-width: 768px) {
  .login-page {
    flex-direction: row;
  }
}

.left-section {
  flex: 1;
  background-color: #e9f035;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.placeholder-image {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: clamp(1rem, 2.5vw, 1.25rem);
  font-weight: bold;
  background-image: url('../assets/images/bg-tectudao.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}


.right-section {
  flex: 1;
  padding: 20px 40px;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.right-section h1 {
  font-family: Goldplay;
  font-weight: 600;
  font-size: clamp(1.5rem, 4vw, 2rem);
  margin-top: 0px;
  margin-bottom: 20px;
  color: #333;
}

.right-section h5 {
  font-family: Goldplay;
  font-weight: 400;
  font-size: clamp(0.875rem, 2vw, 1rem);
  margin-bottom: 0px;
  color: #333;
}

.input-field label {
  font-family: Goldplay;
  font-weight: 400;
  text-align: left;
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
  font-size: clamp(0.875rem, 2vw, 1.15rem);
}

.input-field input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

button {
  font-family: Goldplay;
  font-weight: 400;
  font-size: clamp(0.875rem, 2vw, 1.15rem);
  background-color: #e9f035;
  color: #333;
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}

button:hover {
  background-color: #c4cf24;
  transform: scale(1.05);
}

.extra-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  margin-bottom: 15px;
}

@media (min-width: 768px) {

  .input-field label {
    font-size: 1rem;
  }

  .extra-options {
    flex-direction: row;
    justify-content: space-between;
  }
}

.extra-options a {
  color: #e9f035;
  text-decoration: none;
  margin: 5px 0;
}

.extra-options a:hover {
  text-decoration: underline;
}

.input-field input.error {
  border: 2px solid red; /* Borda vermelha para campos com erro */
}

.error-message {
  color: red;
  font-size: 0.875rem;
  margin-top: 5px; /* Espaço entre o campo e a mensagem de erro */
  text-align: left;
  width: 100%;
  padding-left: 10px; /* Alinhamento com o campo */
}

.input-field {
  position: relative;
}

.input-field input.error + .error-message {
  position: absolute;
  bottom: -20px; /* A mensagem de erro ficará logo abaixo do campo */
  left: 0;
  width: 100%;
}
